import React, { Component } from "react";
import { TextInputProps, TextInput } from "grommet";

type NairaInputProps = {
    onChange: ({ value, formatted, target }: {value: string, formatted: string, target: NairaInput}) => void
};

export default class NairaInput extends Component<NairaInputProps> {
    state = {
        formatted: "",
        value: "",
        num: 0
    };

    render() {
        return (
            <TextInput
                value={this.state.formatted}
                onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                    event.persist();
                    let value = event.target.value;
                    let n = value.replace("₦ ", "").replace(/,/g, "");
                    let num = Number(n);

                    if (Number.isNaN(num)) { num = 0; };

                    value = num.toString();

                    this.setState({formatted: "₦ " + num.toLocaleString(), value, num}, () => {
                        this.props.onChange({value: this.state.value, formatted: this.state.formatted, target: this});
                    })
                }}
            />
        )
    }
}