export default Deposit;

const Paystack = () => {

	const size = useContext(ResponsiveContext);

	const { userState } = useContext(UserContext);

	const [loading, setLoading] = useState(false);

	const [amountValid, setAmountValid] = useState(false);
	const [szAmount, setSzAmount] = useState("");
	const [amount, setAmount] = useState("" as any as number);
	const [fee, setFee] = useState("" as any as number);
	const [total, setTotal] = useState("" as any as number);
	const [error, setError] = useState(false);
	const [snackbar, setSnackbar] = useState({ show: false, message: "Okay now", variant: "success" });

	const [checkoutWithPaystack, setCheckoutWithPaystack] = useState(false);
	const [checkoutLocation, setCheckoutLocation] = useState("");

	let space = size == "small" ? "        " : "                         ";

	const paymentChannel = userState.paymentChannels.find((channel) => channel.name === "Paystack");

	const setPrice = (event: React.ChangeEvent<HTMLInputElement>) => {
		setError(false);
		const a = Number(event.target.value);

		if (isNaN(a)) {
			return;
		}
		// Get the percentage
		let percentage = 0;
		let f = 0;
		let fixedFee = 0;
		if (paymentChannel) {
			if (paymentChannel.usesFeePercentage) {
				percentage = a * (paymentChannel.feePercentage / 100);
			}
			if (paymentChannel.usesFixedFee) {
				fixedFee = paymentChannel.fixedFee;
			}
			f += percentage + fixedFee;

			const t = f + a;
			setAmount(a);
			setFee(f);
			setTotal(t);
		}
	};
	const submitAmount = async () => {
		setLoading(true);
		setError(false);
		if (amount < 100) {
			setError(true);
			setLoading(false);
			return;
		}

		try {
			const data = {
				amount,
				paymentChannel: paymentChannel && paymentChannel.name,
			};
			const response = await Axios.post("/api/account/Wallet/deposit", data);
			if (response.status === 202) {
				setCheckoutWithPaystack(true);
				setCheckoutLocation(response.data);
				
				// const tab = window.open(response.data, "_blank");
				// if (tab) {
				// 	tab.focus();
				// }
			} else if (response.status === 200) {
				setSnackbar({
					message: "Deposit was successful and will be reviewed within 24 hours",
					show: true,
					variant: "success",
				});
			}
		} catch (error) {
			const err = error as AxiosError;
		}
		setLoading(false);
	};

	const onChange = useCallback((name: string) => (event: React.ChangeEvent<HTMLInputElement>) => {
		if (name === "amount") {
			let val = event.target.value.replace("₦ ", "").replace(/,/g, "");
			if (val === "") { val = "0"; }

			const num = Number(val);

			if (Number.isNaN(num) || num > 500000) {
				setAmountValid(false);
				return;
			}

			setAmount(num);
			setSzAmount("₦ " + num.toLocaleString());
			setAmountValid(num >= 100);
		}
	}, [amount, szAmount]);

	return (
		<>

			<Box
				pad="large"
				width={size !== "small" ? "50vw" : "100vw"}
				margin={{ top: "small" }}
				round={true}
			>
				<SnackBarComponent
					message={snackbar.message}
					show={snackbar.show}
					variant={snackbar.variant}
					onClose={() => setSnackbar((s) => ({ ...s, show: false }))}
				/>

				<ProgressBar show={loading as any} />
				{paymentChannel && (
					<>
						<Box
							width="100%"
							align="center"
							direction="row"
						>
							<Box
								style={{
									flexBasis: "100px",
								}}
								height="100px"
								direction="row"
								justify="center"
								round="small"
								background="white"
								margin={{ right: "medium" }}
								pad="medium"
								elevation="medium"
							>
								<Image
									fit="contain"
									src={paymentChannel.logo}
								/>
							</Box>
							<Text
								size={size !== "small" ? "48px" : "16px"}
							>
								{paymentChannel.name}
							</Text>
						</Box>
					</>

				)}
				<Box
					pad="large"
					margin={{ vertical: "large" }}
					background="white"
					round="small"
					width="80vw"
					elevation="medium"
				>
					<PaystackPopup open={checkoutWithPaystack} url={checkoutLocation}/>
					
					<Box
						direction="column"
						align="start"
						margin={{ bottom: "small" }}
						width="100%"
					>
						<Form
							style={{
								width: "100%",
							}}
						>
							<Text
								size="16px"
								weight={100}
							>
								Amount
							</Text>
							<TextInput
								focusIndicator={true}
								value={szAmount}
								onChange={onChange("amount")}
							/>
						</Form>
					</Box>

					<Box
						direction="column"
						align="start"
						width={size !== "small" ? "100px" : "40%"}
					>
						<Form
							style={{
								width: size !== "small" ? "100px" : "40%",
							}}
						>
							{/* <Text
								size="16px"
								weight={100}
							>
								Fee
							</Text>
							<TextInput
								value={fee}
								onFocus={(event) => {
									event.target.blur();
								}}
							/> */}
						</Form>
					</Box>

					<Box
						direction="row"
						justify="end"
					>
						{/* <Heading level="3">Total: ₦{total.toLocaleString()}</Heading> */}
						<Heading level="3">Total: ₦{amount.toLocaleString()}</Heading>
					</Box>
					<Box width="100%" round={true} direction="row" justify="end">
						<Button
							style={{
								color: "white",
								marginTop: "1rem",
							}}
							label={"Proceed"}
							color="secondary"
							onClick={submitAmount}
							primary={true}
							disabled={!amountValid}
						/>
					</Box>
				</Box>
			</Box>

		</>
	);
};

interface IBankPayments {
	amount: number;
	created: Date;
	platformBankAccount: IUserBank;
	transactionDate: Date;
	description: string;
	status: string;
	userBankAccount: IUserBank;
}







const Bank = () => {
	const size = useContext(ResponsiveContext);

	const { userState, userDispatch } = useContext(UserContext);

	const [loading, setLoading] = useState(false);

	const [amount, setAmount] = useState("" as string | number);
	const [fee, setFee] = useState("" as any as number);
	const [total, setTotal] = useState("" as any as number);
	const [platformBankAccounts, setPlatformBankAccounts] = useState([] as IUserBank[]);
	const [bankPayments, setBankPayments] = useState([] as IBankPayments[]);
	const [canSubmit, setCanSubmit] = useState(false);
	const [snackbar, setSnackbar] = useState({ show: false, message: "Okay now", variant: "success" });

	const paymentChannel = userState.paymentChannels.find((channel) => channel.name === "Bank");

	const [paymentDate, setPaymentDate] = useState(new Date);
	const [fromAccount, setFromAccount] = useState({} as IUserBank);
	const [toAccount, setToAccount] = useState({} as IUserBank);
	const [isDirect, setIsDirect] = useState(false);

	const [popupState, setPopupState] = useState({} as AssertivePopupProps);


	let space = size == "small" ? "         " : "                   ";

	let actionsCount = 3;
    const actionComplete = () => {
        actionsCount--;

        if (actionsCount <= 0) {
            setLoading(false);
        }
    }

	const getUserBanks = () => {
		Axios.get<IUserBank[]>("/api/Account/bankAccounts")
			.then((res) => {
				if (res.status) {
					for (const account of res.data) {
						account.bank = BankList[account.bankId - 1]; 
					} 

					if (res.data.length > 0) {
						setFromAccount(res.data[0]);
						setIsDirect(true);
					}

					userDispatch({ type: "UPDATE", payload: { banks: res.data } });
				}
			}).finally(() => actionComplete());
	}

	const getPlatformAccounts = () => {
		Axios.get<IUserBank[]>("/api/account/Wallet/deposit/platformBankAccounts")
			.then((res) => {
				for (const account of res.data) {
					account.bank = BankList[account.bankId - 1]; 
				} 

				if (res.data.length > 0) {
					setToAccount(res.data[0]);
				}				

				setPlatformBankAccounts(res.data);
			}).finally(() => actionComplete());

			// TODO: Critical Error here
	}

	const makePayment = (date: Date, amount: string, isDirect: boolean = false, description: string = "") => {
		if (!date) {
			date = new Date();
		}

		let data: any = {
			date, amount, 
			paymentChannel: "Bank", 
			platformBankAccountId: toAccount.id,
			description
		}

		if (isDirect) {
			data.userBankAccountId = fromAccount.id;
			data.description = "";
		}

		setLoading(true);
		Axios.post("/api/account/Wallet/deposit", data).then((res) => {
			setSnackbar({
				show: true,
				variant: "success",
				message: "Your payment information has been successfully submitted. It will be reviewed and evaluated within 48 hours",
			});
		}).catch((err) => {
			setSnackbar({
				show: true,
				variant: "error",
				message: "An error occured, please try again in a few moments",
			});
		}).finally(() => {
			actionsCount = 1;
			getRequests();
		})
	}

	const getRequests = () => {
		Axios.get<IBankPayments[]>("/api/account/Wallet/deposit/requests")
			.then((res) => {
				setBankPayments(res.data);
			}).finally(() => actionComplete());
	}

	useEffect(() => {
		setLoading(true);

		getUserBanks();
		getPlatformAccounts();
		getRequests();
	}, []);


	const submit = async () => {
		setLoading(true);
		try {
			const data = {
				amount,
				paymentChannel: paymentChannel && paymentChannel.name,
				platformBankAccountId: toAccount.id,
				userBankAccountId: fromAccount.id,
			};
			const res = await Axios.post("/api/account/Wallet/deposit", data);
			if (res.status === 202) {
				setSnackbar({
					message: "Successful",
					show: true,
					variant: "success",
				});
			}
		} catch (error) {/* No code */ }
		setLoading(false);
	};

	// Check if input ;can be submitted before updating the state
	if (toAccount.accountNumber === "" || toAccount.accountNumber === "") {
		if (canSubmit) {
			setCanSubmit(false);
		}
	} else {
		if (!canSubmit) {
			setCanSubmit(true);
		}
	}

	return (
		<>
			<Box
				pad="large"
				width={size !== "small" ? "50vw" : "100vw"}
				margin={{ top: "small" }}
				round={true}
			>
				<SnackBarComponent
					message={snackbar.message}
					show={snackbar.show}
					variant={snackbar.variant}
					onClose={() => setSnackbar((s) => ({ ...s, show: false }))}
				/>
				
				<ProgressBar show={loading} />

				<AssertivePopup 
					size={size}
					contentStyle={{
						height: size === "small" ? "320px" : "350px",
					}}
					title={popupState.title} open={popupState.open} 
					message={popupState.message} action={popupState.action}
					
					onClose={() => {
						setPopupState({open: false, message: "", action: () => {}})
					}}
				/>

				{paymentChannel && (
					<>
						<Box
							width="100%"
							align="center"
							direction="row"
						>
							<Box
								style={{
									flexBasis: "100px",
								}}
								height="100px"
								direction="row"
								justify="center"
								round="small"
								background="white"
								margin={{ right: "medium" }}
								pad="medium"
								elevation="medium"
							>
								<Image
									fit="contain"
									src={paymentChannel.logo}
								/>
							</Box>
							<Heading style={{fontWeight: 300}}>
								{paymentChannel.name}
							</Heading>
						</Box>
					</>

				)}

					<Box
						pad="large"
						margin={{ vertical: "large" }}
						background="white"
						round="small"
						width="100%"
						elevation="medium"
						style={{minHeight: "400px", minWidth: size === "small" ? "initial" : "400px"}}
					>
						{!loading ? (
							<Box>
							<Box
								direction="column"
								align="start"
								margin={{ bottom: "small" }}
								width="100%"
							>
								<Form
									style={{
										width: "100%",
									}}
									onSubmit={(event: any) => {
										setPopupState({
											open: true,
											title: "Create a payment ticket?",
											message: "The payment information will be reviewed before your account is credited." 
												+ " Are you sure you would like to create this payment ticket?", 
											action: () => makePayment(event.value.date, event.value.amount
												, event.value.isDirect, event.value.description)})
									}}
									
								>
									<Box direction="column" align="start" justify="between">
										<Text size="small" style={{margin: "6px 12px"}}>Date Of Payment</Text>
										<FreeFormField
											name="date"
											component={CalendarDropDown}
											date={paymentDate}
											onChange={(event: any) => {
												setPaymentDate(event.value);
											}}
										/>
									</Box>

									<FormField
										name="amount"
										label="Amount Paid"
										component={NairaInput}
										onChange={(event: any) => {
											setAmount(event.formatted);
										}}
										validate={(value) => {
											const num = Number(value);
											if (!value) {
												return "Please enter a valid amount";
											}

											if (Number.isNaN(num)) {
												return "Only numbers are allowed";
											}
											return "";
										}}
									/>

									<Box direction="row" align="stretch">
										<Box flex="grow" width={size == "small" ? "90%" : "initial"}>
											<FreeFormField
												name="to"
												label="Account Paid To"
												value={toAccount.accountName + space 
													+ toAccount.accountNumber + space
													+ (toAccount.bank ? toAccount.bank.knownAs : "")}
												component={Select}
												options={platformBankAccounts.map(b => b.accountName 
													+ space + b.accountNumber 
													+ space + b.bank!.knownAs)}
												onChange={(option : any) => {
													setToAccount(platformBankAccounts[option.selected]);
												}}
											/>
										</Box>

										{ toAccount.bank ? (
											<Box flex="shrink" width="auto" justify="center" align="center" alignContent="center" alignSelf="end">
												<Box align="end" height="50px"
														justify="center"
													margin={{vertical: "3px", bottom: size == "small" ? "initial" : "14px", horizontal: "small"}}
												>
													<Image height={size == "small" ? "16px" : "32px"} fit="contain" src={toAccount.bank.logo} style={{
														maxWidth: size == "small" ? "30px" : "80px"
													}}/>
												</Box>
											</Box>
										): null}
									</Box>

									<Box style={{
										// Min Height is used with validation in mind
										minHeight: "110px"
									}}>
										{
											isDirect ? (
											<Box direction="row" align="stretch">
												<Box flex="grow" width={size == "small" ? "90%" : "initial"}>
													<FreeFormField
														name="from"
														label="Account Paid From"
														value={fromAccount.accountName + space 
															+ fromAccount.accountNumber + space 
															+ fromAccount.bank!.knownAs}
														component={Select}
														options={userState.banks.map(b => b.accountName 
															+ space + b.accountNumber 
															+ space + b.bank!.knownAs)}
														onChange={(option : any) => {
															setFromAccount(userState.banks[option.selected]);
														}}
														style={{
															fontSize: size == "small" ? "12px" : "14px"
														}}
													/>
												</Box>

												{fromAccount.bank ? (
													<Box flex="shrink" width="auto" justify="center" alignSelf="end">
														<Box align="end" height="50px"
															margin={{vertical: "3px", bottom: size == "small" ? "initial" : "14px", horizontal: "small"}}
														>
															<Image height={size == "small" ? "16px" : "32px"} fit="contain" src={fromAccount.bank.logo} style={{
																	maxWidth: size == "small" ? "30px" : "80px"
																}}/>
														</Box>
													</Box>
												): null}
											</Box>
										) : (
											<Box>
												<FreeFormField 
													name="description"
													label="Description"
													component={TextArea}
													placeholder="Enter details about the transaction. Example: 
													GT Bank
													1234567889
													John Doe"
													style={{
														fontWeight: "normal",
													}}
													//formNoValidate={isDirect}
													validate={(value: string, context: any) => {
														if (context.isDirect) { return ""; }

														if (!value) {
															return "Please describe the transaction"
														}

														if (value.length < 20) {
															return "Your description must not be less than 20 characters"
														}
														
														return "";
													}}
												/>
											</Box>
										)}
									</Box>

									<Box width="100%" gap="medium" align="center" justify="end" direction="row">
										<Box margin={{bottom: "small"}}>
											{!(userState.banks && userState.banks.length > 0) ? (
												<Box direction="row" gap="small">
													<Box width="20px">
														<i className="zwicon-info-circle" style={{color: "#9069EB"}}/>
													</Box>
													
													<Text>Add your bank accounts in the <NavAnchor path="/dashboard/settings" color="#9069EB">settings page </NavAnchor> 
													to simplify this payment process</Text>
												</Box>
											) : (
												<Text>I made the payment using my bank account</Text>
											)}
										</Box>
										<FreeFormField
											name="isDirect"
											checked={isDirect}
											value={isDirect}
											component={CheckBox} toggle={true}
											disabled={!(userState.banks && userState.banks.length > 0)}
											onChange={(event: any) => setIsDirect(event.target.checked)}
										/>
									</Box>


									<Box
										direction="column"
										align="start"
										gap="small"
									>
										<Heading level="3">Summary</Heading>

										<Box direction="column">
											<Box direction="row">
												<Box width={size == "small" ? "100px" : "150px"}>
													<Text>
														<strong>Date:</strong>
													</Text>
												</Box>

												<Text>{moment(paymentDate).format("LL")}</Text>
											</Box>

											<Box direction="row">
												<Box width={size == "small" ? "100px" : "150px"}>
													<Text>
														<strong>Amount:</strong>
													</Text>
												</Box>

												<Text>{amount}</Text>
											</Box>

											<Box direction="row">
												<Box width={size == "small" ? "130px" : "150px"}>
													<Text>
													<strong>Account Name:</strong>
													</Text>
												</Box>

												<Text>{toAccount.accountName}</Text>
											</Box>

											<Box direction="row">
												<Box width={size == "small" ? "120px" : "150px"}>
													<Text>
													<strong>Account Number:</strong>
													</Text>
												</Box>

												<Text>{toAccount.accountNumber}</Text>
											</Box>
										</Box>

										<Box direction="row">
											<Box width={size == "small" ? "100px" : "150px"}>
												<Text>
													<strong>Bank:</strong>
												</Text>
											</Box>

											{toAccount.bank ? (
												<Box direction="row" gap="small">
													<Image height="20px" fit="contain" src={toAccount.bank.logo} style={{
															maxWidth: "40px"
														}}/>
													<Text>{toAccount.bank.name}</Text>
												</Box>
											) : null}
										</Box>
									</Box>

									<Box width="100%" margin={{top: "medium"}}>
										<Button primary={true} color="secondary"
											 label="Submit" type="submit"
											 style={{
												 color: "#fff"
											 }}
										/>
									</Box>
								</Form>

								<Box margin={{top: "large"}} width="100%">
									<Text weight={300} size={size == "small" ? "12px" : "initial"}>
										* Please make sure that all information you have entered above is accurate, 
										and that the payment has already been successfully made before you submit 
										this payment ticket. If you <strong>don't</strong> comply with this instruction, 
										you may be <strong>locked out</strong> of your account
									</Text>
								</Box>
							</Box>
						</Box>
						) : null}
					</Box>

				
			</Box>
			

			<Box justify="start" width="100%" margin={{ bottom: "large" }}>
				<Heading style={{fontWeight: 300}}>Bank Payments</Heading>
			</Box>

			{bankPayments.map((b, i) => (
				<Box
					key={i}
					background="white"
					elevation="small"
					round="small"
					direction="row"
					pad={{ vertical: "medium", horizontal: "medium" }}
					margin={{ bottom: "medium" }}
					justify={size !== "small" ? "evenly" : "between"}
					align="center"
					width={size !== "small" ? "70vw" : "95vw"}
				>
					<Text
						style={{
							width: size !== "small" ? "20%" : "14%",
						}}
					>
						{moment(b.transactionDate).format("LL")}
					</Text>
					<Text>
						{"₦ " + b.amount.toLocaleString()}
					</Text>
					<Text
						style={{
							width: "100px",
						}}
					>
						{b.platformBankAccount ? 
							b.platformBankAccount.accountName
							+ "         " + 
							b.platformBankAccount.accountNumber 
							+ "         " + 
							BankList[b.platformBankAccount.bankId - 1].knownAs
							: null 
						}
					</Text>
					{size !== "small" && (
						<Text
							style={{
								width: "100px",
							}}
						>
							{b.userBankAccount ? 
								b.userBankAccount.accountName 
								+ "        " +
								b.userBankAccount.accountNumber 
								+ "        " + 
								BankList[b.userBankAccount.bankId - 1].knownAs
								: b.description
							}
						</Text>
					)}
					<Text
						color={b.status === "Pending" ? "status-warning" : "secondary"}
					>
						{b.status}
					</Text>
				</Box>
			))}
		</>
	);
};
